* {
  margin: 0;
}
body {
  font-family: sans-serif;
}
.App {
  text-align: center;
  max-width: 728px;
  margin: 0 auto;
  background-color: rgb(24, 23, 23);
}
section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
}
header {
     background-color: rgb(0, 106, 255);
     height: 10vh;
     min-height: 50px;
     position: fixed;
     top: 0;
     width: 100%;
     max-width: 728px;
     display: flex;
     justify-content: space-between;
     padding: 10px;
     box-sizing: border-box;
}
header img {
     height: 40px;
     width: 40px;
     cursor: pointer;
     margin-top: -5px;
}
h2 {
     font-size: 18px;
     margin-top: auto;
     margin-bottom: auto;
     font-weight: 500;
     letter-spacing: 1px;
     cursor: pointer;
     margin-right: auto;
     margin-left: 10px;
}
#logo {
     margin-top: auto;
     margin-bottom: auto;
     border-radius: 50%;
}
.chat-window {
     height: 81vh;
     padding: 10px;
     margin: 10vh 0 10vh;
     background-color: rgb(24, 23, 23);
     background-size: contain;
     display: flex;
     flex-direction: column;
     overflow-y: scroll;
}
form {
     height: 9vh;
     position: fixed;
     bottom: 0;
     background-color: rgb(233, 233, 233);
     width: 100%;
     max-width: 728px;
     font-size: 1rem;
     display: flex;
}
form button {
     width: 20%;
     background-color: rgb(233, 233, 233);
     border: none;
     display: flex;
     justify-content: center;
     align-items: center;
     height: 50px;
}
form button i {
     font-size: 18px;
     margin-top: -5px;
}
form input {
     line-height: 1.5;
     width: 100%;
     font-size: 1.5rem;
     background-color: rgb(233, 233, 233);
     outline: none;
     color: #111;
     height: 50px;
     font-size: 15px;
     border: none;
     padding-left: 10px;
     border-right: 1px solid grey;
}
.message {
     display: flex;
     align-items: center;
}
.message img {
     width: 40px;
     height: 40px;
     border-radius: 50%;
     margin: 2px 5px;
}
.message p {
     max-width: 500px;
     margin-bottom: 12px;
     line-height: 24px;
     padding: 10px 20px;
}
.sent {
     flex-direction: row-reverse;
}
.sent p {
     color: white;
     background-color: #0b93f6;
     align-self: flex-end;
     border-radius: 20px;
}
.recived p {
     background: #e5e5ea;
     color: black;
     border-radius: 20px;
}
#signIn {
     background-color: rgb(24, 23, 23);
}
.signIn-btn {
     color: black;
     display: flex;
     align-items: center;
     background-color: white;
     border-radius: 10px;
     box-shadow: 1px 1px 10px 5px rgb(20, 20, 20);
     border: none;
     max-width: 400px;
     margin: 0 auto;
     padding: 7px 30px;
     cursor: pointer;
}
.signIn-btn img {
     width: 40px;
     height: 40px;
     margin-right: 14px;
}
p {
     color: white;
     text-align: center;
     letter-spacing: 1px;
}
